	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>Canada has the longest coastline of any country in the world, fronting on three oceans, and four great lakes;</p>\n</li>\n<li>\n<p>Canada needs to reaffirm its position as a leading state in sustainable oceanic research, anddevelopment;</p>\n</li>\n<li>\n<p>Canada's capacity for oceanic and Arctic scientific research has been diminished by a failure to renew our oceanographic research vessels;</p>\n</li>\n<li>\n<p>high capacity submarine fiber optics telecommunications cables are currently being laid to connect Europe and Asia, through the Canadian Arctic without provision of access by the Canadian Government or Canadian Arctic communities, which are restricted to slow and expensive satellite internet services;</p>\n</li>\n<li>\n<p>the current national ocean policy \"Canada's ocean strategy\" needs to be updated to reflect the realities of today.</p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Canadian government:</p>\n<ul>\n<li>\n<p>commission a 21st century comprehensive integrated three-Oceans Policy, including environmental protection, economic and social development, scientific research, sovereignty and security;</p>\n</li>\n<li>\n<p>implement a strategy to re-establish Canada's oceanographic research capabilities, including acquiring oceanographic research vessels for the short, medium, and long term;</p>\n</li>\n<li>\n<p>for purposes of security, sovereignty and Arctic community development, make full access to telecommunications (internet) capacity a requirement for any trans-Arctic telecommunications cables transiting through Canada's Arctic waters.(Nova Scotia)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/national-three-oceans-policy-for-security%2Cenvironmental-protection-and-development","date":"Mon Mar 05 2018 23:27:04 GMT-0500 (EST)","title":"National Three-Oceans Policy for Security,Environmental Protection and Development"}}},"pathContext":{}}

